@import url(https://fonts.googleapis.com/css?family=Pacifico:700|Open+Sans:300,400);
* {
  box-sizing: content-box;
  margin: 0;
  padding: 0; }

.gnome__list {
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(1, 95%);
  grid-gap: 60px;
  -webkit-justify-content: center;
          justify-content: center; }

.gnome__notfound {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 100vh;
  width: 100vw; }

.gnome__notfound-text {
  font-size: 12px; }

.gnome {
  background-color: rgba(199, 252, 236, 0.5);
  border-radius: 5%; }
  .gnome:hover {
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22); }

.glowButton {
  padding: 0 14px;
  transition: all .3s ease; }

.glowButton:hover {
  background: rgba(234, 236, 236, 0.72);
  border-color: #00A984;
  box-shadow: 0 0 25px 1px white; }

@media all and (min-width: 768px) {
  .gnome__list {
    grid-template-columns: repeat(2, 50%);
    grid-gap: 20px; } }

@media all and (min-width: 1200px) {
  .gnome__list {
    grid-template-columns: repeat(3, 25%);
    grid-gap: 30px; }
  .gnome__notfound-text {
    font-size: 50px;
    color: #E9FEF7; } }

.gnome__lists {
  display: grid;
  grid-template-columns: repeat(2, 120px);
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 5%;
  line-height: 1.7; }

.gnome__list-professions {
  list-style-type: none;
  text-align: center; }

.gnome__list-friends {
  list-style-type: none;
  text-align: center; }

.gnome__list-friendstitle {
  text-align: center; }

.gnome__list-professionstitle {
  text-align: center; }

.gnome__image-wrapper {
  padding-top: 0;
  width: 100%;
  height: 250px; }

.gnome__image {
  filter: hue-rotate(0deg) drop-shadow(10px 10px 10px gray);
  -webkit-filter: hue-rotate(36deg) saturate(1.5);
  max-width: 100%;
  max-height: 100%;
  position: relative;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%); }

.gnome__card {
  height: 580px;
  max-width: 300px; }

.gnome__name {
  text-align: center;
  background-color: #E9FEF7;
  max-width: 100vw; }

.gnome__details {
  padding: 7px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  border: solid 1px white; }

.app__filter-item {
  margin-top: 5%;
  background: #E9FEF7;
  padding: 5px; }

.app__filter-fullname {
  border: 0;
  padding: 10px;
  width: 100%;
  box-sizing: border-box;
  font-weight: bold;
  font-size: 18px;
  outline: none;
  text-align: center; }

.loader {
  color: aquamarine;
  font-size: 90px;
  text-indent: -9999em;
  overflow: hidden;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  display: block;
  position: fixed;
  z-index: 1031;
  top: calc( 50% - ( 1em / 2));
  right: calc( 50% - ( 1em / 2));
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
  -webkit-animation: load6 1.7s infinite ease, round 1.7s infinite ease;
          animation: load6 1.7s infinite ease, round 1.7s infinite ease; }

